import { getArticleComment } from '@/api/article';
export default {
  name: 'ArticleComment',
  props: {
    article: {
      type: Object,
      default: {},
      required: true
    }
  },
  data() {
    return {
      comments: []
    }
  },
  // 客户端渲染，mounted
  async mounted() {
    const { data } = await getArticleComment(this.article.slug);
    this.comments = data.comments;
  }
}
